import {Component} from 'vue-property-decorator'
import Tabs from '@/components/Tabs.vue';
import recordTypeList from '@/constants/recordTypeList';
import dayjs from 'dayjs';
import clone from '@/lib/clone';
import _ from 'lodash'
import day from 'dayjs'
// const ECharts: any= require('vue-echarts').default; // 在 webpack 环境下指向 components/ECharts.vue
// import 'echarts/lib/chart/line'
// import 'echarts/lib/component/polar'
// import * as ECharts from 'echarts/lib/echarts';
import Chart from '@/components/Chart.vue'
import ECharts from 'echarts';

import 'echarts/lib/chart/line'

// console.log('ECharts');
// console.log(ECharts);
// console.log(ECharts.version);

@Component({
  components: {Tabs,Chart}
})
export default class Statistics extends Vue{
  tagString(tags: Tag[]) {
    return tags.length === 0 ? '无' :
        tags.map(t => t.name).join('，');
  }

  mounted(){
    const div = this.$refs.chartWrapper as HTMLDivElement;
    div.scrollLeft = div.scrollWidth;
  }

  beautify(string: string) {
    const day = dayjs(string);
    const now = dayjs(); //now是一个api
    // console.log(now);
    if (day.isSame(now, 'day')) {
      return '今天';
    } else if (day.isSame(now.subtract(1, 'day'), 'day')) {
      // console.log('hi');
      return '昨天';
    } else if (day.isSame(now.subtract(2, 'day'), 'day')) {
      return '前天';
    } else if (day.isSame(now, 'year')) {
      return day.format('M月D日');
    } else {
      return day.format('YYYY年M月D日');
    }
  }

  get keyValueList() {
    const today = new Date();
    const array = [];
    for (let i = 0; i <= 29; i++) {
      // this.recordList = [{date:7.3, value:100}, {date:7.2, value:200}]
      const dateString = day(today)
          .subtract(i, 'day').format('YYYY-MM-DD');
      const found = _.find(this.groupedList, {
        title: dateString
      });

      array.push({
        key: dateString, value: found ? found.total : 0
      });
    }
    array.sort((a, b) => {
      if (a.key > b.key) {
        return 1;
      } else if (a.key === b.key) {
        return 0;
      } else {
        return -1;
      }
    });
    return array;
  }

  get chartOptions(){
    const keys = this.keyValueList.map(item => item.key);
    const values = this.keyValueList.map(item => item.value);
    return {
      grid: {
        left: 0,
        right: 0,
      },
      xAxis: {
        type: 'category',
        data: keys,
        axisTick: {alignWithLabel: true},
        axisLine: {
          lineStyle: {color: '#666'}
        },
        axisLabel:{
          formatter: function (value:string, index:number){
            return value.substr(5)
          }
        }
      },
      yAxis: {
        type: 'value',
        show: false
      },
      series: [{
        symbol: 'circle',
        symbolSize: 12,
        itemStyle: {borderWidth: 1, color: '#EE6363', },
        // lineStyle: {width: 10},
        data: values,
        type: 'line'
      }],
      tooltip: {
        show: true, triggerOn: 'click',
        position: 'top',
        formatter: '{c}'
      }
    };
  }

  get recordList() {
    return (this.$store.state as RootState).recordList;
  }

  get groupedList() {
    const {recordList} = this;
    // type HashTableValue = { title: string, items: RecordList[] }

    // const hashTable: { [key: string]: HashTableValue } = {};

    // for (let i = 0; i < recordList.length; i++) {
    //   const [date, time] = recordList[i].createdAt!.split('T');
    //   hashTable[date] = hashTable[date] || {title: date, items: []};
    //   hashTable[date].items.push(recordList[i]);
    // }

    //js数组方法sort会修改原数组
    const newList = clone(recordList)
        .filter(r => r.type === this.type)
        .sort((a, b) => dayjs(b.createdAt).valueOf() - dayjs(a.createdAt).valueOf());
    if (newList.length === 0) { return []; }
    //定义result的类型
    type Result = { title: string, total?: number, items: RecordItem[] }[]

    const result: Result = [
        {title: dayjs(newList[0].createdAt).format('YYYY-MM-DD'),
          items: [newList[0]]
        }
    ];

    for (let i = 1; i < newList.length; i++) {
      const current = newList[i];
      const last = result[result.length - 1];
      if (dayjs(last.title).isSame(dayjs(current.createdAt), 'day')) {
        last.items.push(current);
      } else {
        result.push( {title: dayjs(current.createdAt).format('YYYY-MM-DD'), items: [current]} );
      }
    }

    result.map(group => {
      group.total = group.items.reduce( (sum, item) => {
        console.log(sum);
        console.log(item);
        return sum + item.amount;
      }, 0);
    });
    return result;
    // get result(){
      // [
      //  {title,items}
      //  {title,items}
      // ]
    // }
  }

  beforeCreate() {
    this.$store.commit('fetchRecords'); //调用方法  用commit
  }

  type = '-';
  // interval = 'day';
  // intervalList = intervalList;
  recordTypeList = recordTypeList;
};
